  --bg: hsl(0deg 0% 100% / 20%);
  --white: hsl(0deg 0% 100%);

  --blur: 10px;
  --indent: 4ch;
}

body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: grid;
  justify-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat;
  background-size: cover;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: var(--white);
  text-align: justify;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Tangerine", serif;
  font-weight: bold;
  text-align: center;
  text-decoration: underline;
  margin: 1em;
}

ul {
  list-style: square;
}

li {
  margin: .5em;
}

h1 {
  font-size: 3rem;
  margin-top: 0;
}

h2 {
  font-size: 2rem;
}

header {
  background: var(--bg);
  backdrop-filter: blur(var(--blur));
  padding: 1rem;
  width: 100%;
}

nav {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

button {
  background: none;
  border: none;
  border-bottom: 1px solid transparent;
  cursor: pointer;
  padding: .5rem 0;
  color: var(--white);
  &:hover, &.selected {
    border-color: var(--white);
  }
}

#content {
  margin: 2rem;
  width: min(65ch, calc(100% - 4rem));
  overflow-y: overlay;
  padding: calc(2rem + 10px);
  background: var(--bg);
  backdrop-filter: blur(var(--blur));
  border-radius: 1rem;
  scrollbar-width: thin;
  scrollbar-color: var(--bg) transparent;
}

.menu-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
  margin-bottom: 4rem;
}

.menu-item img {
  flex: 1;
  min-width: 0;
  max-width: 300px;
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: 1rem;
  border: 1px solid var(--white);
  transition: all .5s;
  cursor: pointer;
  &:hover {
    scale: 1.025;
  }
}

.price {
  font-family: "Tangerine", serif;
  font-size: 1.5rem;
  line-height: 0;
}

.employee-title, .week-day {
  font-weight: 700;
}

.employee-name {
  font-family: "Tangerine", serif;
  font-size: 2rem;
}

:last-child {
  margin-bottom: 0;
}

/* UTILITY CLASSES */

.indent {
  text-indent: var(--indent);
}

.text-center {
  text-align: center;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,6BAA6B;EAC7B,0BAA0B;;EAE1B,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,gBAAgB;EAChB,aAAa;EACb,qBAAqB;EACrB,0BAA0B;EAC1B,mCAAmC;EACnC,6DAAsD;EACtD,sBAAsB;EACtB,gCAAgC;EAChC,gBAAgB;EAChB,kBAAkB;EAClB,eAAe;EACf,mBAAmB;EACnB,mBAAmB;AACrB;;AAEA;EACE,+BAA+B;EAC/B,iBAAiB;EACjB,kBAAkB;EAClB,0BAA0B;EAC1B,WAAW;AACb;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,eAAe;EACf,aAAa;AACf;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,qBAAqB;EACrB,kCAAkC;EAClC,aAAa;EACb,WAAW;AACb;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,SAAS;AACX;;AAEA;EACE,gBAAgB;EAChB,YAAY;EACZ,oCAAoC;EACpC,eAAe;EACf,gBAAgB;EAChB,mBAAmB;EACnB;IACE,0BAA0B;EAC5B;AACF;;AAEA;EACE,YAAY;EACZ,mCAAmC;EACnC,mBAAmB;EACnB,0BAA0B;EAC1B,qBAAqB;EACrB,kCAAkC;EAClC,mBAAmB;EACnB,qBAAqB;EACrB,sCAAsC;AACxC;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,UAAU;EACV,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,YAAY;EACZ,gBAAgB;EAChB,eAAe;EACf,iBAAiB;EACjB,mBAAmB;EACnB,8BAA8B;EAC9B,mBAAmB;EACnB,eAAe;EACf;IACE,YAAY;EACd;AACF;;AAEA;EACE,+BAA+B;EAC/B,iBAAiB;EACjB,cAAc;AAChB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,+BAA+B;EAC/B,eAAe;AACjB;;AAEA;EACE,gBAAgB;AAClB;;AAEA,oBAAoB;;AAEpB;EACE,0BAA0B;AAC5B;;AAEA;EACE,kBAAkB;AACpB","sourcesContent":[":root {\n  --bg: hsl(0deg 0% 100% / 20%);\n  --white: hsl(0deg 0% 100%);\n\n  --blur: 10px;\n  --indent: 4ch;\n}\n\nbody {\n  width: 100vw;\n  height: 100vh;\n  overflow: hidden;\n  display: grid;\n  justify-items: center;\n  grid-template-columns: 1fr;\n  grid-template-rows: min-content 1fr;\n  background: url('../images/background.jpeg') no-repeat;\n  background-size: cover;\n  font-family: \"Inter\", sans-serif;\n  font-weight: 400;\n  font-style: normal;\n  font-size: 16px;\n  color: var(--white);\n  text-align: justify;\n}\n\nh1, h2, h3, h4, h5, h6 {\n  font-family: \"Tangerine\", serif;\n  font-weight: bold;\n  text-align: center;\n  text-decoration: underline;\n  margin: 1em;\n}\n\nul {\n  list-style: square;\n}\n\nli {\